import { Dispatch, SetStateAction } from "react"

type ObserverResizeProps = {
  componentRef: HTMLDivElement | null
  setWidth: Dispatch<SetStateAction<number>>
}

function observerResize({ setWidth, componentRef }: ObserverResizeProps): void {
  if (!componentRef) return

  setWidth(componentRef.offsetWidth)
}

export { observerResize }
